import axiosInstance from "@/core/services/axios/axiosInstance";
import { ChangePasswordResponse, LoginResponse,SignUpResponse,SecretKeyResponse } from "../types/auth.types";
import { getEndpoint } from "@/core/constant/apis";

export interface LoginCredentials {
  email: string;
  password: string;
}

export interface SecretKeyCredentials {
    user_id:number;
    secret_key:	string;
}

export interface SignUpCredentials {
  first_name: string;
  last_name: string;
  email: string;
  password: string;
  phone_number: string,
  country_code: string,
  mac_address:string|null;
  role_id: number;
  is_active:boolean;
  started_on:string;
}

export interface CreateUserCredentials {
  first_name: string;
  last_name: string;
  email: string;
  // password: string;
  phone_number: string,
  country_code:string,
  // mac_address:string|null;
  role_id: number;
  is_active:boolean;
  started_on:string;
}

export interface ChangePasswordRequest {
  newPassword: string;
  confirmPassword: string;
  token:string;
}

export const authApi = {
  loginUser: async ({
    email,
    password,
  }: LoginCredentials): Promise<LoginResponse> => {
    const { data } = await axiosInstance.post<LoginResponse>(
      getEndpoint('AUTH.LOGIN'),
      { email, password }
    );

    if (!data.access_token || !data.refresh_token || !data.user_id) {
      throw new Error('Invalid login response');
    }

    axiosInstance.defaults.headers.common["Authorization"] = `Bearer ${data.access_token}`;
    return data;
  },
  SecretKeyUser: async ({
    user_id,
    secret_key,
  }: SecretKeyCredentials): Promise<SecretKeyResponse> => {
    const { data } = await axiosInstance.post<SecretKeyResponse>(
      getEndpoint('AUTH.SECRET_KEY'),
      {  user_id,
        secret_key,}
    );

    if (!data) {
      throw new Error('Invalid response');
    }
    return data;
  },
  SignUpUser: async ({
    email,
    password,
    first_name,
      last_name,
      phone_number, // <-- now a string
      country_code,
      mac_address,
      role_id,
      is_active,
      started_on,
  }: SignUpCredentials): Promise<SignUpResponse> => {
    const { data } = await axiosInstance.post<SignUpResponse>(
      getEndpoint('AUTH.SIGN_UP'),
      {
        email,
        password,
        first_name,
          last_name,
          phone_number, // <-- now a string
          country_code,
          mac_address,
          role_id,
          is_active,
          started_on,
      }
    );

    if (!data.user_id || !data.message) {
      throw new Error('Invalid signup response');
    }
    console.log(data)
 return data;
  },

  CreateUser: async ({
    email,
    // password,
    first_name,
      last_name,
      phone_number, // <-- now a string
      country_code,
      // mac_address,
      role_id,
      is_active,
      started_on,
  }: CreateUserCredentials): Promise<SignUpResponse> => {
    const { data } = await axiosInstance.post<SignUpResponse>(
      getEndpoint('AUTH.SIGN_UP'),
      {
        email,
        // password,
        first_name,
          last_name,
          phone_number, // <-- now a string
          country_code,
          // mac_address,
          role_id,
          is_active,
          started_on,
      }
    );

    if (!data.user_id || !data.message) {
      throw new Error('Invalid signup response');
    }
    console.log(data)
 return data;
  },
 
logoutUser: async (): Promise<void> => {
    await axiosInstance.post(getEndpoint('AUTH.LOGOUT'));
  },
  forgotPassword: async (email: string): Promise<void> => {
    await axiosInstance.post(getEndpoint('AUTH.FORGOT_PASSWORD'), { email });
  },
  changePassword: async ({newPassword, confirmPassword,token }: ChangePasswordRequest): Promise<ChangePasswordResponse> => {
    const { data } = await axiosInstance.post(getEndpoint("AUTH.CHANGE_PASSWORD"), {newPassword, confirmPassword,token });
    return data;
  },
};
