import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { authApi } from "../api/authApi";
import { logout } from "../store/authSlice";
import { toast } from "sonner";
import { CommonModal } from "@/components/ui/CommonModal";
import { cn } from "@/utils/cn";
import { Eye,Plus } from "lucide-react";
import CreateUserForm from "./CreateUserForm";
import CreateWorkSpaceForm from "./CreateWorkSpaceForm";

interface CreateWorkSpaceWrapperProps {
  children: React.ReactNode;
}

interface AddNewWorkSpaceMenuItemProps {
  onClick: () => void;
  icon?: React.ElementType;
}

export const AddNewWorkSpaceMenuItem: React.FC<AddNewWorkSpaceMenuItemProps> = ({ onClick, icon: Icon }) => {
  return (
    <>
   <CreateWorkSpaceWrapper >
    <li className="list-none">
      <a
        onClick={onClick}
        className={cn(
          "flex justify-between gap-2 py-2 px-4 rounded-full text-sm cursor-pointer",
          "bg-white text-[#0575E6] border-blue-300"
        )}
      >
        <span>Add Workspace</span>
        {Icon && <Icon className="h-5 w-5" />}
      </a>
    </li>
    </CreateWorkSpaceWrapper>
    </>
  );
};

const CreateWorkSpaceWrapper: React.FC<CreateWorkSpaceWrapperProps> = ({ children }) => {
  const dispatch = useDispatch();
  const [isLoading, setIsLoading] = useState(false);

  const handleCreateWorkspace = async () => {
    setIsLoading(true);
    // dispatch(logout());
    try {
      // await authApi.logoutUser();
      // dispatch(logout());
      // toast.success("Logged out successfully");
    } catch (error) {
      // toast.error("Failed to logout");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    
     <CommonModal
     title=""
     description={
        <div>
          <CreateWorkSpaceForm />
        </div>
     }
    onConfirm={handleCreateWorkspace}
    isLoading={isLoading}
   >
         <li className="list-none">
           <a
            //  onClick={onClick}
             className={cn(
               "flex justify-between py-3 px-4 rounded-full text-sm cursor-pointer w-full",
               "bg-white text-[#0575E6] border-blue-300"
             )}
           >
             <span className="flex justify-between  gap-2 wfull">Add Workspace </span>
             <Plus className="h-5 w-5"/>
           </a>
         </li>
   </CommonModal>
  );
};

export default CreateWorkSpaceWrapper;
