interface SidebarProps {
  navItems: NavItem[];
  setIsSidebarOpen: (open: boolean) => void;

}

interface NavItem {
  title: string;
  path?: string;
  icon?: React.ElementType;
  dropdown?: { title: string; path: string }[];
}

const AddNewUserMenuItem = memo(({ onClick, icon: Icon }: { onClick: () => void; icon?: React.ElementType }) => (
  <CreateUserWrapper>
    <li className="list-none">
      <a
        onClick={onClick}
        className={cn("flex justify-between gap-2 rounded-full text-sm cursor-pointer bg-white text-[#0575E6] border-blue-300")}
      >
        <span>Add New User</span>
        {Icon && <Icon className="h-5 w-5" />}
      </a>
    </li>
  </CreateUserWrapper>
));

const AddNewWorkSpaceMenuItem = memo(({ onClick, icon: Icon }: { onClick: () => void; icon?: React.ElementType }) => (
  <CreateWorkSpaceWrapper>
    <li className="list-none">
      <a
        onClick={onClick}
        className={cn("flex justify-between gap-2 py-2 px-4 rounded-full text-sm cursor-pointer bg-white text-[#0575E6] border-blue-300")}
      >
        <span>Add workspace</span>
        {Icon && <Icon className="h-5 w-5" />}
      </a>
    </li>
  </CreateWorkSpaceWrapper>
));

const AddNewSKUItem = memo(({ icon: Icon, open }: { icon?: React.ElementType; open?: boolean }) => {
  // const [isDialogOpen, setIsDialogOpen] = useState(open ?? false); // open on mount if true
  const [isLoading, setIsLoading] = useState(false);

  // useEffect(() => {
  //   if (open) {
  //     setIsDialogOpen(true); // auto-open if activated
  //   }
  // }, [open]);

  const handleCreateSKU = async () => {
    setIsLoading(true);
    try {
      toast.success("SKU created successfully");
      // setIsDialogOpen(false);
    } catch (error) {
      toast.error("Failed to create SKU");
    } finally {
      setIsLoading(false);
    }
  };

  const handleClick = () => {
    if (!open) {
      toast.error("Workspace is not activated");
      return;
    }
    // setIsDialogOpen(true);
  };

  return (
    <ConfirmationUser
      title=""
      description={<div><CreateNewSkuForm /></div>}
      onConfirm={handleCreateSKU}
      isLoading={isLoading}
    // open={false}
    // onOpenChange={setIsDialogOpen}
    >
      <a
        onClick={handleClick}
        className={cn(
          "text-white flex justify-between gap-2 py-2 px-2 rounded-full text-sm cursor-pointer",
          // " border-white p-2 border-[0.2px] rounded-lg"
        )}
      >
        <span className="flex items-center gap-2">
          {Icon && <Icon className="h-5 w-5" />}
          Create SKUs
        </span>
      </a>
    </ConfirmationUser>
  );
});


const DetailPanelFieldEngineer = ({ row }: { row: any }) => {


  const showDanger = row.failed_activation_attempts > 2 && !row.is_activated;

  return (
    <div className="p-4 rounded-md transition-all duration-300">
      <h3 className="font-medium text-lg mb-4">
        Workspace: {row.name}
      </h3>

      <div className="space-y-2">
        <>
          <PhoneVerificationInput workspaceId={row.id} />
          <ActivationValidationInput workspaceId={row.id} />
        </>
      </div>
    </div>
  );
};

const PhoneVerificationInput = ({ workspaceId }: { workspaceId: number }) => {
  const [phoneNumber, setPhoneNumber] = useState("");
  const [loading, setLoading] = useState(false);

  const handleVerifyPhone = async () => {
    setLoading(true);
    try {
      const res = await fetch("http://localhost:8001/api/field-assistant-verification/", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          workspace_id: workspaceId,
          phone_number: phoneNumber,
        }),
      });
      const data = await res.json();
      alert(` Phone verified: ${data.message}`);
    } catch (error) {
      alert(" Phone verification failed");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="flex flex-col space-y-2">
      <input
        type="text"
        placeholder="Engineer Phone Number *"
        className="border p-1 rounded-full"
        value={phoneNumber}
        onChange={(e) => setPhoneNumber(e.target.value)}
      />
      {/* <button
        className="bg-blue-500 text-white p-1 rounded-full disabled:opacity-50"
        onClick={handleVerifyPhone}
        disabled={loading || !phoneNumber}
      >
        {loading ? "Verifying..." : "Verify"}
      </button> */}
    </div>
  );
};


const ActivationValidationInput = ({ workspaceId }: { workspaceId: number }) => {
  const [activationKey, setActivationKey] = useState("");
  const [loading, setLoading] = useState(false);

  const handleValidateKey = async () => {
    setLoading(true);
    try {
      const res = await fetch("http://localhost:8001/api/workspace-validate/", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          workspace_id: workspaceId,
          activation_key: activationKey,
        }),
      });
      const data = await res.json();
      alert(` Activation validated: ${data.message}`);
    } catch (error) {
      alert(" Activation validation failed");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="flex flex-col space-y-2 my-2">
      <input
        type="text"
        placeholder="Activation Key *"
        className="border p-1 rounded-full"
        value={activationKey}
        onChange={(e) => setActivationKey(e.target.value)}
      />
      <button
        className="bg-green-600 text-white p-1 rounded-full disabled:opacity-50"
        onClick={handleValidateKey}
        disabled={loading || !activationKey}
      >
        {loading ? "Validating..." : "Validate"}
      </button>
    </div>
  );
};
const WorkSpaceValidation = memo(({ row, icon: Icon, open }: { icon?: React.ElementType }) => {
  // const [isLoading, setIsLoading] = useState(false);
  // const [open, setOpen] = useState(true); //  opens automatically
  const [isDialogOpen, setIsDialogOpen] = useState(open ?? false); // open on mount if true
  const [isLoading, setIsLoading] = useState(false);

  // useEffect(() => {
  //   if (open) {
  //     setIsDialogOpen(true); // auto-open if activated
  //   }
  // }, [open]);

  const handleCreateSKU = async () => {
    setIsLoading(true);
    try {
      toast.success("SKU created successfully");

    } catch (error) {
      toast.error("Failed to create SKU");
    } finally {
      setIsLoading(false);
      setIsDialogOpen(false);
      // setOpen(false); // close dialog on confirm
    }
  };

  return (
    <ConfirmationUser
      title=""
      description={<DetailPanelFieldEngineer row={row} />}
      onConfirm={handleCreateSKU}
      isLoading={isLoading}
      open={open}
      onOpenChange={setIsDialogOpen}
    >
      {/* <p className={`text-md text-center text-gray-500}`}> */}
      {/* {skus.length < 5
            ? "Click to add more SKUs"
            : "Contact Indus Vision to add more SKUs"}
          <br />
          {skus.length}/5 left to create */}
      {/* No Data to show */}
      {/* </p> */}
    </ConfirmationUser>
  );
});

const Sidebar: React.FC<SidebarProps> = ({ navItems, setIsSidebarOpen }) => {
  const navigate = useNavigate();
  const location = useLocation();
  const [activeItem, setActiveItem] = useState<string | null>(null);
  const [openDropdown, setOpenDropdown] = useState<string | null>(null);
  const { id, workspaceId, skuId } = useParams();
  console.log(workspaceId)
  console.log(skuId)

  // const filteredWorkspaces = initialWorkSpaceData?.results.filter(
  //   (workspace) => workspace.id == workspaceId
  // );
  // console.log(filteredWorkspaces)
  // const [workspaceData, setWorkspaceData] = useState<any[]>(filteredWorkspaces[0] || []);

  const [workspaceData, setWorkspaceData] = useState<any[]>([]);
  const fetchWorkspace = async () => {

    try {
      const response = await axios.get(
        `https://ac4ee0054010e6ee7a052c425e401a13.serveo.net/api/workspace/${workspaceId}/`,
        {
          headers: {
            Accept: 'application/json',
          },
        }
      );
      console.log(response.data)

      if (response.data?.results) {
        setWorkspaceData(response.data.results);
      } else {
        setWorkspaceData([]); // fallback if no results key
      }
    } catch (err) {
      console.log(err)

    }
  };

  useEffect(() => {
    fetchWorkspace();
  }, []);

  useEffect(() => {
    console.log('Workspace data updated:', workspaceData);
  }, [workspaceData]);

  console.log(workspaceData)

  useEffect(() => {
    const currentItem = navItems.find((item) => item.path === location.pathname);
    if (currentItem) setActiveItem(currentItem.title);
  }, [location.pathname, navItems]);

  // const handleNavigation = useCallback(
  //   (path: string, itemTitle: string) => {
  //     setActiveItem(itemTitle);
  //     navigate(path);
  //     if (window.innerWidth < 1024) setIsSidebarOpen(false);
  //   },
  //   [navigate, setIsSidebarOpen]
  // );

  const handleNavigation = useCallback(
    (path: string, itemTitle: string) => {
      setActiveItem(itemTitle);

      // Use current searchParams and override only `tab` if it's present in the new path
      const currentParams = new URLSearchParams(window.location.search);
      const incomingUrl = new URL(path, window.location.origin);
      const incomingTab = incomingUrl.searchParams.get("tab");

      if (incomingTab) {
        currentParams.set("tab", incomingTab);
      }

      const newPath = `${incomingUrl.pathname}?${currentParams.toString()}`;

      navigate(newPath);
      if (window.innerWidth < 1024) setIsSidebarOpen(false);
    },
    [navigate, setIsSidebarOpen]
  );



  return (
    <aside
      className="mx-h-screen overflow-hidden h-screen shadow-lg fixed top-0 left-0 flex flex-col transition-all z-30 w-56 gap-16"
      style={{ background: "linear-gradient(219.95deg, #435FCD -2.18%, #062393 61.84%, #020B2D 132.87%)" }}
    >
      <div className="text-center flex justify-center items-center p-4">
        <span className="block text-3xl font-bold text-white">NEX</span>
        <span className="block text-3xl font-bold text-green-600">IFY</span>
      </div>

      {/* Decorative Circles - Consider moving to CSS or removing on mobile */}
      <div className="absolute top-[500px] left-[-410px] hidden lg:block">
        <div className="rounded-full border border-blue-500 w-[500px] h-[500px]"></div>
        <div className="absolute top-6 left-16 rounded-full border border-blue-500 w-[500px] h-[500px]"></div>
      </div>

      <nav className="px-3 mt-4">
        <ul className="space-y-4">
          {navItems.map((item, idx) => (
            <li key={idx}
              className={`list-none ${["SKU data", "Annotate", "Indus Vision AI", "Performance", "Deploy","Evaluate"].includes(item.title)
                ? ""
                : "border-white p-2 border-[0.2px] rounded-lg"}`
              }
            // className="list-none border-white p-2 border-[0.2px] rounded-lg"
            >
              {item.dropdown ? (
                <div className="flex flex-col">
                  {/* Parent item */}
                  <div
                    className={cn(
                      "flex text-white items-center justify-between w-full py-3 px-2 rounded-md cursor-pointer ",
                      openDropdown === item.title ? "bg-muted/10" : "hover:bg-muted/10"
                    )}
                    onClick={() => setOpenDropdown(openDropdown === item.title ? null : item.title)}
                  >
                    <span className="flex items-center gap-2 text-sm">
                      {item.icon && <item.icon className="h-5 w-5" />}
                      {item.title}
                    </span>
                    <ChevronDown className={cn("w-4 h-4 transition-transform", openDropdown === item.title && "rotate-180")} />
                  </div>

                  {/* Dropdown items */}
                  {openDropdown === item.title && (
                    <ul className="ml-6 mt-2 space-y-1">
                      {item.dropdown.map((subItem, subIdx) => {
                        if (subItem.title === "Add New user") {
                          return (
                            <AddNewUserMenuItem
                              key={subIdx}
                              onClick={() => handleNavigation(subItem.path, subItem.title)}
                              icon={item.icon}
                            />
                          );
                        }
                        if (subItem.title === "Add new workspace") {
                          return (
                            <AddNewWorkSpaceMenuItem
                              key={subIdx}
                              onClick={() => handleNavigation(subItem.path, subItem.title)}
                              icon={item.icon}
                            />
                          );
                        }
                        return (
                          <li key={subIdx} className="list-none">
                            <a
                              onClick={() => handleNavigation(subItem.path, subItem.title)}
                              className="flex items-center gap-2 px-2 py-2 text-sm transition-colors cursor-pointer text-white bg-primary rounded-full"
                            >
                              {item.icon && <item.icon className="h-4 w-4" />}
                              <span>{subItem.title}</span>
                            </a>
                          </li>
                        );
                      })}
                    </ul>
                  )}
                </div>
              ) : (
                // Non-dropdown items
                <>
                  {item.title === "Create SKUs" ? (
                    <>
                      <AddNewSKUItem icon={item.icon} open={false} />
                      {!workspaceData?.is_activated && (
                        <WorkSpaceValidation row={workspaceData} open />
                      )}
                    </>
                  ) : ["SKU data", "Annotate", "Indus Vision AI", "Performance", "Deploy"].includes(item.title) ? (
                    <button
                      onClick={() => item.path && handleNavigation(item.path, item.title)}
                      className="bg-primary hover:bg-green-600 text-white py-2 px-4 rounded-full w-full text-left"
                    >
                      {item.title}
                    </button>
                  ) : (
                    <a
                      onClick={() => item.path && handleNavigation(item.path, item.title)}
                      className={cn(
                        "flex items-center gap-2 text-sm text-white rounded-md px-2 py-2 cursor-pointer transition-colors",
                        activeItem === item.title ? "bg-muted/10" : "hover:bg-muted/10"
                      )}
                    >
                      {item.icon && <item.icon className="h-4 w-4" />}
                      {item.title}
                    </a>
                  )}
                </>
              )}
            </li>
          ))}
        </ul>
      </nav>


      {/* <nav className="px-3 mt-4">
        <ul className="space-y-4">
          {navItems.map((item, idx) => (
            <>
              {item.dropdown ? (
                <>
                  <li key={idx} className="list-none border-white p-2 border-[0.2px] rounded-lg">
                    <div
                      className={cn(
                        "flex text-white items-center justify-between w-full py-3 cursor-pointer ",
                        openDropdown === item.title && "text-white"
                      )}
                      onClick={() => setOpenDropdown(openDropdown === item.title ? null : item.title)}
                    >
                      <span className="flex items-center gap-2 text-sm">
                        {item.icon && <item.icon className="h-5 w-5" />}
                        {item.title}
                      </span>
                      <ChevronDown className={cn("w-4 h-4 transition-transform", openDropdown === item.title && "rotate-180")} />
                    </div>
                  </li>

                  {openDropdown === item.title && (
                    <ul className="space-y-1">
                      {item.dropdown.map((subItem, subIdx) => {
                        if (subItem.title === "Add New user") {
                          return <AddNewUserMenuItem key={subIdx} onClick={() => handleNavigation(subItem.path, subItem.title)} icon={item.icon} />;
                        }
                        if (subItem.title === "Add new workspace") {
                          return <AddNewWorkSpaceMenuItem key={subIdx} onClick={() => handleNavigation(subItem.path, subItem.title)} icon={item.icon} />;
                        }
                        return (
                          <li key={subIdx} className="list-none  border-white p-2 border-[0.2px] rounded-lg">
                            <a
                              onClick={() => handleNavigation(subItem.path, subItem.title)}
                              className={cn("flex justify-between gap-2 py-2 px-4 text-white bg-primary rounded-full border-blue-300 text-sm cursor-pointer")}
                            >
                              <span>{subItem.title}</span>
                              {item.icon && <item.icon className="h-5 w-5" />}
                            </a>
                          </li>
                        );
                      })}
                    </ul>
                  )}
                </>
              ) : (
                <>
                  {item.title === "Create SKUs" ? (
                    <>
                      <AddNewSKUItem icon={item.icon} open={false} />
                      {!workspaceData?.is_activated ? (
                        <>
                          <WorkSpaceValidation row={workspaceData} open={!workspaceData?.is_activated} />
                        </>
                      ) : null}
                    </>
                  ) : ["SKU data", "Annotate", "Indus Vision AI", "Performance", "Deploy"].includes(item.title) ? (
                    <button
                      onClick={() => item.path && handleNavigation(item.path, item.title)}
                      className="bg-primary hover:bg-green-600 text-white py-2 px-4 rounded-full w-full text-left">
                      {item.title}
                    </button>
                  ) : (
                    <a
                      onClick={() => item.path && handleNavigation(item.path, item.title)}
                      className={cn(
                        "flex flex-row items-center text-sm text-center rounded-md cursor-pointer transition-colors text-white  border-white p-2 border-[0.2px] rounded-lg",
                        activeItem === item.title ? "text-white" : "hover:bg-muted/20",
                        item.title.length > 16 ? "py-1" : "py-3"
                      )}
                    >
                      <span className="flex items-center gap-2">
                        {item.icon && <item.icon className="h-4 w-4" />}
                        {item.title}
                      </span>
                    </a>
                  )}
                </>
              )}
            </>
          ))}
        </ul>
      </nav> */}
    </aside>
  );
};

export default memo(Sidebar);
