import { useMemo } from "react";
import { Routes, Route, Navigate } from "react-router-dom";
import { useSelector } from "react-redux";
import { publicRoutes } from "./RoutesConfig";
import { DEFAULT_ROUTES } from "@/core/constant/routes";
import { UserRole } from "@/features/auth/types/auth.types";
import { RootState } from "@/store";
import NotFoundPage from "@/components/common/NotFoundPage";
import { ClientRoutes } from "./ClientRoutes";
// import { CoAdminRoutes } from "./CoAdminRoutes";
// import { AdminRoutes } from "./AdminRoutes";

export const AppRoutes = () => {
  const selectUser = useMemo(
    () => (state: RootState) => state.auth.role_name,
    []
  );
  const user = useSelector(selectUser);
  console.log(user)

  const getDefaultRoute = (userRole?: UserRole | null): string => {
    return userRole ? DEFAULT_ROUTES[userRole] ?? "/login" : "/login";
  };
  

  return (
    <Routes>
      {publicRoutes.map(({ path, element: Element }) => (
        <Route key={path} path={path} element={<Element />} />
      ))}
      <Route path="/client/*" element={<ClientRoutes/>} />
      {/* <Route path="/admin-dashboard/*" element={<CoAdminRoutes />} /> */}
      {/* <Route path="/admin/*" element={<AdminRoutes/>} /> */}
      <Route
        path="/"
        element={<Navigate to={getDefaultRoute(user)} replace />}
      />
      <Route path="*" element={<NotFoundPage />} />
    </Routes>
  );
};
