// export default PhoneInput;


import React, { useEffect, useState } from "react";
import { Input } from "@/components/ui/input";
import { countries } from "@/components/ui/countries";

type Props = {
  value: string;
  onChange: (value: string, countryCode: string) => void;
  hasError?: boolean;
};

const PhoneInput = ({ value, onChange, hasError }: Props) => {
  const defaultCountry =
    countries.find((c) => value?.startsWith(c.dial_code)) || countries[0];

  const [selectedCountry, setSelectedCountry] = useState(defaultCountry);
  const [numberOnly, setNumberOnly] = useState(() =>
    value?.replace(defaultCountry.dial_code, "") || ""
  );
  const [isDropdownOpen, setIsDropdownOpen] = useState(false);
  const [searchTerm, setSearchTerm] = useState("");

  const filteredCountries = countries.filter(
    (c) =>
      c.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      c.code.toLowerCase().includes(searchTerm.toLowerCase())
  );

  useEffect(() => {
    // Ensure initial state is reflected in the form
    onChange(numberOnly, selectedCountry.dial_code);
  }, []);

  useEffect(() => {
    // Sync the dial code if value is present
    const dialCode = selectedCountry.dial_code;
    if (!value.startsWith(dialCode)) {
      onChange(numberOnly, dialCode);
    }
  }, [selectedCountry]);
  

  const handleNumberChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const max = selectedCountry.maxLength ?? 15;
    const digits = e.target.value.replace(/\D/g, "").slice(0, max);
    setNumberOnly(digits);
    onChange(digits, selectedCountry.dial_code);
  };

  const handleCountrySelect = (countryCode: string) => {
    const country = countries.find((c) => c.code === countryCode);
    if (country) {
      setSelectedCountry(country);
      setNumberOnly("");
      onChange("", country.dial_code);
      setSearchTerm("");
      setIsDropdownOpen(false);
    }
  };

  return (
    <div className="w-full relative">
      <div className="flex items-center gap-2">
        {/* Country selector */}
        <button
          type="button"
          onClick={() => setIsDropdownOpen(!isDropdownOpen)}
          className="border py-2 pl-2 pr-6 text-sm flex items-center gap-2 rounded-full"
        >
          <img
            src={selectedCountry.image}
            alt={`${selectedCountry.code} flag`}
            className="w-5 h-5 object-cover rounded-sm"
            title={`${selectedCountry.name} (${selectedCountry.unicode})`}
          />
          <span>{selectedCountry.dial_code}</span>
        </button>

        {/* Phone number input */}
        <Input
          value={numberOnly}
          onChange={handleNumberChange}
          type="tel"
          className="rounded-full"
          hasError={hasError}
          placeholder="Phone Number*"
        />
      </div>

      {/* Country dropdown */}
      {isDropdownOpen && (
        <div className="absolute z-50 bg-white border rounded-md shadow-md mt-1 w-72 max-h-60 overflow-y-auto">
          <input
            autoFocus
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            placeholder="Search country"
            className="w-full border-b px-3 py-2 text-sm focus:outline-none"
          />
          {filteredCountries.map((country) => (
            <div
              key={country.code}
              onClick={() => handleCountrySelect(country.code)}
              className="px-3 py-2 hover:bg-gray-100 cursor-pointer text-sm flex justify-between items-center"
            >
              <span className="flex gap-2 items-center">
                <img
                  src={country.image}
                  alt={`${country.code} flag`}
                  title={`${country.emoji} (${country.unicode})`}
                  className="w-5 h-5 object-cover rounded-sm"
                />
                {country.name}
              </span>
              <span className="text-muted-foreground">{country.dial_code}</span>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default PhoneInput;
