import React from "react";
import { cn } from "@/utils/cn";
import useGetCurrentUser from "@/hooks/useGetCurrentUser";
import { toTitleCase } from "@/utils/textFormater";
import AccountMenu from "@/components/common/AccountMenu";

interface HeaderProps {
  isSidebarOpen: boolean;
  setIsSidebarOpen: (isOpen: boolean) => void;
  className: string;
}

const WorkspaceHeader: React.FC<HeaderProps> = ({
  isSidebarOpen,
  setIsSidebarOpen,
  className,
}) => {
  const { role } = useGetCurrentUser();
  return (
    <nav
      className={cn(
        `bg-background fixed top-0 right-0 border-b border-gray-200 h-[70px]  z-40`,
        className
      )}
    >
        
      <div className="sm:px-6 lg:px-8 flex items-center h-16">
         <div className="text-center flex justify-center items-center p-4">
        <span className="block text-3xl font-bold text-[#062393]">NEX</span>
        <span className="block text-3xl font-bold text-green-600">IFY</span>
      </div>
        {/* Spacer to push icons to the right */}
        <div className="flex-1">
          <div className="block">
            <h1 className="text-2xl font-bold">{role && toTitleCase(role)}</h1>
          </div>
        </div>

        {/* Notification and Logout Buttons (Right) */}
        <div className="flex items-center space-x-4">
          <AccountMenu />
        </div>
      </div>
    </nav>
  );
};

export default WorkspaceHeader;
