import {
  LayoutDashboard,
  ClipboardList,
  Eye,
  Plus,
  FolderCode,
  FileUser,
} from "lucide-react";

const userPrefix = "/client";

// A function to determine the icon based on the tab name
const getIconForTab = (tab: string) => {
  switch (tab) {
    case "SKU data":
      return FileUser;
    case "Annotate":
      return ClipboardList;
    case "Indus Vision AI":
      return Eye;
    case "Performance":
      return FolderCode;
    case "Deploy":
      return Plus;
    default:
      return FolderCode;
  }
};

export const ClientSideNavItems = (
  isSkuDetail: boolean,
  workspaceId: string,
  skuId: string
) => {
  const tabs = ["SKU data", "Annotate", "Indus Vision AI", "Performance", "Deploy"];

  const baseItems = [
    { title: "Workspace Name", path: `${userPrefix}/workspaces`, icon: LayoutDashboard },
    { title: "Create SKUs", path: `${userPrefix}/workspaces/${workspaceId}`, icon: FolderCode },
  ];

  if (!isSkuDetail) return baseItems;

  const skuTabs = tabs.map((tab) => ({
    title: `${tab.charAt(0).toUpperCase() + tab.slice(1)}`,
    path: `${userPrefix}/workspaces/${workspaceId}/sku/${skuId}?tab=${tab}`,
    icon: getIconForTab(tab),
  }));

  const baseItems2 = [
    { title: "Workspace Name", path: `${userPrefix}/workspaces`, icon: LayoutDashboard },
    { title: "View All SKUs", path: `${userPrefix}/workspaces/${workspaceId}`, icon: FolderCode },
  ];
  return [...baseItems2, ...skuTabs];
};
