import { useMutation } from "@tanstack/react-query";
import axios from "axios";
import { CreateSkuSchema } from "../../auth/schemas/createSku.schema";

export const useCreateSku = () => {
    return useMutation({
        mutationFn: async (data: CreateSkuSchema) => {
            const response = await axios.post("https://ac4ee0054010e6ee7a052c425e401a13.serveo.net/api/sku/", data); // Replace with actual API URL
            return response.data;
        },
    });
};
