import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { createSkuSchema, CreateSkuSchema } from "../../auth/schemas/createSku.schema";
import { useCreateSku } from "../../client/hooks/useCreateSku";
import { Input } from "@/components/ui/input";
import {
  Form, FormField, FormItem, FormControl, FormMessage
} from "@/components/ui/form";
import { Button } from "@/components/ui/button";
import { Select, SelectTrigger, SelectValue, SelectContent, SelectItem } from "@/components/ui/select";
import { Loader2 } from "lucide-react";
import { toast } from "sonner";
const CreateNewSkuForm = () => {
  const { workspaceId } = useParams<{ workspaceId: string }>();

  //     // Map tag string to number
  // const tagMap: Record<string, number> = {
  //   AnamolyDetection: 1,
  //   CustomDetection: 2,
  // };
  const { mutate, isLoading } = useCreateSku();

  const form = useForm<CreateSkuSchema>({
    resolver: zodResolver(createSkuSchema),
    defaultValues: {
      name: "",
      tag: "AnamolyDetection",
    },
  });
  // State to hold tags fetched from API
  const [tags, setTags] = useState<Tag[]>([]);
  const [loadingTags, setLoadingTags] = useState(true);
  // Fetch tags from your API once on mount
  useEffect(() => {
    async function fetchTags() {
      try {
        const res = await fetch("https://ac4ee0054010e6ee7a052c425e401a13.serveo.net/api/tags/");
        const data = await res.json();
        setTags(data.results.filter((tag: Tag) => tag.is_active)); // filter active tags
      } catch (e) {
        console.error("Failed to fetch tags", e);
      } finally {
        setLoadingTags(false);
      }
    }
    fetchTags();
  }, []);

  // Build a map from tag name to id for quick lookup
  const tagMap: Record<string, number> = {};
  tags.forEach((tag) => {
    tagMap[tag.name] = tag.id;
  });

  const handleSubmit = (values: CreateSkuSchema) => {
    const nowISO = new Date().toISOString();

    const payload = {
      name: values.name,
      created_date_time: nowISO,
      updated_date_time: nowISO,
      workspace: Number(workspaceId),  // convert workspaceId to number, default 0
      tag: tagMap[values.tag],         // convert tag string to number
    };
    console.log(payload)
    mutate(payload, {
      onSuccess: () => {
        toast.success("SKU created successfully");
        form.reset();
        window.location.reload();
      },
      onError: () => {
        toast.error("Failed to create SKU");
      },
    });
  };

  return (
    <Form {...form}>
      <div className="mb-8">
        <div className="text-2xl font-secondary font-bold text-[#062393]">Create a New SKU</div>
        <div className="text-sm font-secondary  font-normal ">Fill all the fields</div>
      </div>
      <form onSubmit={form.handleSubmit(handleSubmit)} className="space-y-6">
        <FormField
          control={form.control}
          name="name"
          render={({ field }) => (
            <FormItem>
              <FormControl>
                <Input {...field} placeholder="Name of SKU*" className="rounded-full w-full" />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />

        <FormField
          control={form.control}
          name="tag"
          render={({ field }) => (
            <FormItem>
              <FormControl>
                <Select
                  value={field.value ?? ""}
                  onValueChange={field.onChange}
                  disabled={loadingTags}
                >
                  <SelectTrigger className="rounded-full">
                    <SelectValue placeholder={loadingTags ? "Loading tags..." : "Select Tag*"} />
                  </SelectTrigger>
                  <SelectContent>
                    {tags.map((tag) => (
                      <SelectItem key={tag.id} value={tag.name}>
                        {tag.name}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />


        {/* <FormField
          control={form.control}
          name="tag"
          render={({ field }) => (
            <FormItem>
              <FormControl>
                <Select
                  value={field.value ?? ""}
                  onValueChange={field.onChange}
                >
                  <SelectTrigger className="rounded-full">
                    <SelectValue placeholder="Select Tag*" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="AnamolyDetection">Anamoly Detection</SelectItem>
                    <SelectItem value="CustomDetection">Custom Detection</SelectItem>
                  </SelectContent>
                </Select>
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        /> */}

        <Button type="submit" disabled={isLoading} className="w-full rounded-full">
          {isLoading ? (
            <>
              <Loader2 className="mr-2 h-4 w-4 animate-spin" />
              Please wait
            </>
          ) : (
            "Create New SKU"
          )}
        </Button>
      </form>
    </Form>
  );
};

export default CreateNewSkuForm;
