import { useMutation } from "@tanstack/react-query";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import { authApi } from "../api/authApi";
import { setCredentials } from "../store/authSlice";
import { DEFAULT_ROUTES } from "../../../core/constant/routes";
import { toast } from "sonner";
import { SignUpResponse } from "../types/auth.types";
import type { CreateUserCredentials } from "../api/authApi"; 


export const useCreateUser = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const { mutate, isPending, error } = useMutation<
  SignUpResponse,
    Error,
    CreateUserCredentials
  >({
    mutationFn: authApi.CreateUser,
    onSuccess: (data) => {
    //   // Store user info in Redux
    //   dispatch(setCredentials({ 
    //     user_id: data.user_id, 
    //   }));

      // Redirect user to correct route based on role
    //   const defaultRoute = DEFAULT_ROUTES[data.user.role.name];
    //   if (defaultRoute) {
    //     toast.success("SignUp successful");
    //     navigate(defaultRoute);
    //   } else {
    //     toast.error("Invalid user role");
    //   }
    console.log(data)
    
          toast.success(data.message || "User created successfully");
            // toast.success("User created successful, secret key sent to email of client!");
            setTimeout(()=>{
              window.location.reload();
            },2000)
    },
    onError: (error: Error) => {
      console.log(error)
      // console.log(error.response.data.message)
      toast.error(error.response.data.message || "User creation failed");
    },
  });

  return { mutate, isLoading: isPending, error };
};
