import {
    AlertDialog,
    AlertDialogAction,
    AlertDialogCancel,
    AlertDialogContent,
    AlertDialogDescription,
    AlertDialogFooter,
    AlertDialogHeader,
    AlertDialogTitle,
    AlertDialogTrigger,
  } from "./alert-dialog";
  
  // interface ConfirmationUserProps {
  //   children: React.ReactNode;
  //   title: string;
  //   description: string;
  //   onConfirm: () => void;
  //   isLoading?: boolean;
  // }

  interface ConfirmationUserProps {
  children: React.ReactNode;
  title: string;
  description: string;
  onConfirm: () => void;
  isLoading?: boolean;
  open?: boolean;
  onOpenChange?: (open: boolean) => void;
}

  
  export function ConfirmationUser({
    children,
    title,
    description,
    onConfirm,
    isLoading = false,
    open,
    onOpenChange
  }: ConfirmationUserProps) {
    return (
      // <AlertDialog >
        <AlertDialog open={open} onOpenChange={onOpenChange}>
        <AlertDialogTrigger asChild>{children}</AlertDialogTrigger>
        <AlertDialogContent className="bg-card max-w-sm sm:max-w-md">
        <AlertDialogHeader className="relative">
        {/* <AlertDialogCancel className="absolute right-0 top-0 p-2 text-red-500 outline-none"> */}
        <AlertDialogCancel className="absolute right-0 top-0 p-1 text-red-500 outline-none border-2 rounded-full py-[3px] px-[10px]">
              X
            </AlertDialogCancel>
            {/* <AlertDialogCancel className="border-none px-2 py-2 shadow-none text-red-500 flex items-end justify-end outline-none">X</AlertDialogCancel> */}
            <AlertDialogTitle>{title}</AlertDialogTitle>
            <AlertDialogDescription>{description}</AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
              {/* <AlertDialogAction
              onClick={onConfirm}
              disabled={isLoading}
              >
              {isLoading ? "Loading..." : "Continue"}
              </AlertDialogAction> */}
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    );
  }
  