
import * as z from "zod";

export const createSkuSchema = z.object({
  name: z.string().min(1, "SKU name is required").max(100, "Maximum 100 characters allowed"),
  created_date_time: z.string().max(100).nullable().optional(),
  updated_date_time: z.string().max(100).nullable().optional(),
  workspace: z.number().nullable().optional(),
  tag: z.string().nullable().optional(),
});

export type CreateSkuSchema = z.infer<typeof createSkuSchema>;
