import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { authApi } from "../api/authApi";
import { logout } from "../store/authSlice";
import { toast } from "sonner";
import { ConfirmationUser } from "@/components/ui/ConfirmationUser";
import { cn } from "@/utils/cn";
import { Eye,Plus } from "lucide-react";
import CreateUserForm from "./CreateUserForm";

interface CreateUserWrapperProps {
  children: React.ReactNode;
}

interface AddNewUserMenuItemProps {
  onClick: () => void;
  icon?: React.ElementType;
}

export const AddNewUserMenuItem: React.FC<AddNewUserMenuItemProps> = ({ onClick, icon: Icon }) => {
  return (
    <>
   <CreateUserWrapper >
    <li className="list-none">
      <a
        onClick={onClick}
        className={cn(
          "flex justify-between gap-2 py-2 px-4 rounded-full text-sm cursor-pointer",
          "bg-white text-[#0575E6] border-blue-300"
        )}
      >
        <span>Add New User</span>
        {Icon && <Icon className="h-5 w-5" />}
      </a>
    </li>
    </CreateUserWrapper>
    </>
  );
};

const CreateUserWrapper: React.FC<CreateUserWrapperProps> = ({ children }) => {
  const dispatch = useDispatch();
  const [isLoading, setIsLoading] = useState(false);

  const handleCreateUser = async () => {
    setIsLoading(true);
    dispatch(logout());
    try {
      // await authApi.logoutUser();
      dispatch(logout());
      toast.success("Logged out successfully");
    } catch (error) {
      toast.error("Failed to logout");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    
     <ConfirmationUser
     title=""
     description={
        <div>
          <CreateUserForm />
        </div>
     }
    onConfirm={handleCreateUser}
    isLoading={isLoading}
   >
         <li className="list-none">
           <a
            //  onClick={onClick}
             className={cn(
               "flex justify-between gap-2 py-3 px-4 rounded-full text-sm cursor-pointer",
               "bg-white text-[#0575E6] border-blue-300"
             )}
           >
             <span className="flex justify-between gap-2">Add New User <Plus className="h-5 w-5"/></span>
             {/* {Icon && <Icon className="h-5 w-5" />} */}
           </a>
         </li>
   </ConfirmationUser>
  );
};

export default CreateUserWrapper;
