import React from "react";
import { useForm } from "react-hook-form";
import { Select, SelectTrigger, SelectValue, SelectContent, SelectItem } from "@/components/ui/select";
import { Input } from "@/components/ui/input";
import { zodResolver } from "@hookform/resolvers/zod";
import { Eye, EyeOff, Loader2 } from "lucide-react";
import { Button } from "@/components/ui/button";
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import PhoneInput from "@/components/ui/phoneinput"; //  Correct for default exports

// import { useSignUp } from "../hooks/useSignUp";
import { useCreateUser } from "../hooks/useCreateUser";
import { createUserSchema, CreateUserSchema } from "../schemas/createUser.schema";
import { Link } from "react-router-dom"; // fixed this from "react-router" to "react-router-dom"

const CreateUserForm = () => {
  const [showPassword, setShowPassword] = React.useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = React.useState(false);
  // const { mutate, isLoading } = useSignUp();
  const { mutate, isLoading } = useCreateUser();


  const form = useForm<CreateUserSchema>({
    resolver: zodResolver(createUserSchema),
    mode: "onSubmit",
    defaultValues: {
      first_name: "",
      last_name: "",
      email: "",
      phone_number: "", // <-- now a string
      country_code: "",
      // password: "",
      // confirm_password: "",
      // mac_address: null,
      role_id: 1,
      is_active: true,
      started_on: new Date().toISOString().slice(0, 10), // format: "YYYY-MM-DD"
    },
  });

  // const handleSignup = (values: CreateUserSchema) => {
  //   const { confirm_password, ...dataToSubmit } = values;
  //   console.log("Submitted data (without confirm_password):", dataToSubmit);
  //   // mutate(dataToSubmit);
  // };

  // const handleSignup = (values: CreateUserSchema) => {
  //   const { confirm_password, phone_number, country_code, ...rest } = values;
  
  //   const dataToSubmit = {
  //     ...rest,
  //     phone_number: `${country_code}${phone_number}`, // Concatenate here
  //     country_code, // Optional to include
  //   };
  
  //   console.log("Final submission data:", dataToSubmit);
  //   mutate(dataToSubmit);
  // };

  const handleSignup = (values: CreateUserSchema) => {
    console.log('click')
    const { phone_number, country_code, ...rest } = values;
  
    const dataToSubmit = {
      ...rest,
      phone_number: `${country_code}${phone_number}`, // Concatenate here
      country_code, // Optional to include
    };
  
    console.log("Final submission data:", dataToSubmit);
    mutate(dataToSubmit);
  };
  


  return (
    <Form {...form}>
      <div className="mb-8">
        <div className="text-2xl font-secondary font-bold text-[#062393]">Create New User!</div>
        <div className="text-sm font-secondary  font-normal ">Fill all the fields</div>

      </div>
      <form onSubmit={form.handleSubmit(handleSignup)} className="space-y-6">
        <div className="flex gap-3">


          {/* First Name */}
          <FormField
            control={form.control}
            name="first_name"

            render={({ field }) => (
              <FormItem>
                {/* <FormLabel>First Name</FormLabel> */}
                <FormControl>
                  <Input {...field}
                    hasError={!!form.formState.errors.first_name}

                    className="rounded-full"
                    placeholder="First Name*" />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />

          {/* Last Name */}
          <FormField
            control={form.control}
            name="last_name"
            render={({ field }) => (
              <FormItem>
                {/* <FormLabel>Last Name</FormLabel> */}
                <FormControl>
                  <Input {...field}
                    hasError={!!form.formState.errors.last_name}

                    className="rounded-full"
                    placeholder="Last Name*" />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
        </div>

        {/* Email */}
        <FormField
          control={form.control}
          name="email"
          render={({ field }) => (
            <FormItem>
              {/* <FormLabel>Email</FormLabel> */}
              <FormControl>
                <Input
                  {...field}
                  type="email"
                  placeholder="Email Address*"
                  autoComplete="off"
                  hasError={!!form.formState.errors.email}
                  className="rounded-full"
                />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />
        <FormField
  control={form.control}
  name="phone_number"
  render={({ field }) => (
    <FormItem>
      <FormControl>
        <PhoneInput
          value={field.value || ""}
          onChange={(numberOnly, countryCode) => {
            field.onChange(numberOnly); // updates phone_number
            form.setValue("country_code", countryCode); // updates country_code
          }}
          
          hasError={!!form.formState.errors.phone_number}
        />
      </FormControl>
      <FormMessage />
    </FormItem>
  )}
/>

        {/* <FormField
          control={form.control}
          name="phone_number"
          render={({ field }) => (
            <FormItem>
              <FormControl>
                <PhoneInput
                  value={field.value || ""}
                  onChange={(numberOnly, countryCode) => {
                    field.onChange(numberOnly); // sets `phone_number`
                    form.setValue("country_code", countryCode); // sets `country_code` field
                  }}
                  hasError={!!form.formState.errors.phone_number}
                />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        /> */}

        {/* 
        <FormField
        control={form.control}
        name="phone_number"
        render={({ field }) => (
            <FormItem>
            {/* <FormLabel>Phone Number</FormLabel>
        <FormControl>
          <PhoneInput
            value={field.value || ""}
            onChange={field.onChange}
            hasError={!!form.formState.errors.phone_number}
          />
        </FormControl>
        <FormMessage />
      </FormItem>
        )}
        /> */}
      <FormField
        control={form.control}
        name="role_id"
        render={({ field }) => (
          <FormItem>
            <FormControl>
              <Select
                value={String(field.value)} // ensure string conversion
                onValueChange={(val) => field.onChange(Number(val))} // convert back to number
              >
                <SelectTrigger className="rounded-full">
                  <SelectValue placeholder="Select Role*" />
                </SelectTrigger>
                <SelectContent>
                  {/* <SelectItem value="2">Admin</SelectItem> */}
                  <SelectItem value="1">Client</SelectItem>
                  <SelectItem value="3">Field Engineers</SelectItem>
                </SelectContent>
              </Select>
            </FormControl>
            <FormMessage />
          </FormItem>
        )}
      />


      {/* <FormField
        control={form.control}
        name="role"
        render={({ field }) => (
            <FormItem>
            {/* <FormLabel>Phone Number</FormLabel> 
            <FormControl>
                <Select
                //   value={field.value}
                onValueChange={field.onChange}
                //   defaultValue={field.value}
                >
                <SelectTrigger className="rounded-full">
                    <SelectValue placeholder="Select Role*" />
                </SelectTrigger>
                <SelectContent>
                    <SelectItem value="2">Admin</SelectItem>
                    <SelectItem value="1">Client</SelectItem>
                    <SelectItem value="3">Field Engineers</SelectItem>
           
                </SelectContent>
                </Select>
            </FormControl>
            <FormMessage />
            </FormItem>
        )}
        />  */}

      {/* Password */}
      {/* <FormField
        control={form.control}
        name="password"
        render={({ field }) => (
          <FormItem>
            {/* <FormLabel>Password</FormLabel> 
            <FormControl>
              <div className="relative">
                <Input
                  {...field}
                  type={showPassword ? "text" : "password"}
                  placeholder="Password*"
                  autoComplete="off"
                  hasError={!!form.formState.errors.password}
                  className="rounded-full"
                />
                <button
                  type="button"
                  className="absolute inset-y-0 right-0 pr-3 flex items-center hover:text-accent-foreground"
                  onClick={() => setShowPassword(!showPassword)}
                >
                  {showPassword ? (
                    <EyeOff className="h-5 w-5 text-muted-foreground" />
                  ) : (
                    <Eye className="h-5 w-5 text-muted-foreground" />
                  )}
                </button>
              </div>
            </FormControl>
            <FormMessage />
          </FormItem>
        )}
      />

      {/* Confirm Password 
      <FormField
        control={form.control}
        name="confirm_password"
        rules={{
          required: "Please confirm your password",
          validate: (value) =>
            value === form.getValues("password") || "Passwords do not match",
        }}
        render={({ field }) => (
          <FormItem>
            {/* <FormLabel>Password</FormLabel> 
            <FormControl>
              <div className="relative">
                <Input
                  {...field}
                  type={showConfirmPassword ? "text" : "password"}
                  placeholder="Confirm Password*"
                  autoComplete="off"
                  hasError={!!form.formState.errors.confirm_password}
                  className="rounded-full"
                />
                <button
                  type="button"
                  className="absolute inset-y-0 right-0 pr-3 flex items-center hover:text-accent-foreground"
                  onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                >
                  {showConfirmPassword ? (
                    <EyeOff className="h-5 w-5 text-muted-foreground" />
                  ) : (
                    <Eye className="h-5 w-5 text-muted-foreground" />
                  )}
                </button>
              </div>
            </FormControl>
            <FormMessage />
          </FormItem>
        )}
      /> */}
      <Button type="submit" disabled={isLoading} className="w-full rounded-full">
        {isLoading ? (
          <>
            <Loader2 className="mr-2 h-4 w-4 animate-spin" />
            Please wait
          </>
        ) : (
          "Create New User"
        )}
      </Button>
    </form>
    </Form >
  );
};

export default CreateUserForm;
