import * as React from 'react';

import Box from '@mui/material/Box';
import Avatar from '@mui/material/Avatar';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Tooltip from '@mui/material/Tooltip';
import PersonAdd from '@mui/icons-material/PersonAdd';
import Settings from '@mui/icons-material/Settings';
import Logout from '@mui/icons-material/Logout';
import { useSelector } from "react-redux";
import { RootState } from "@/store";
import { Bell, Power } from "lucide-react";
import LogoutWrapper from "@/features/auth/components/LogoutWrapper";

export default function AccountMenu() {

  const selectUser = React.useMemo(
    () => (state: RootState) => state.auth,
    []
  );
  const user = useSelector(selectUser);

  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);
  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  return (
    <React.Fragment>
      <Box sx={{ display: 'flex', alignItems: 'center', textAlign: 'center' }}>

        <Tooltip title="Account settings">
          <IconButton
            onClick={handleClick}
            size="small"
            aria-controls={open ? 'account-menu' : undefined}
            aria-haspopup="true"
            aria-expanded={open ? 'true' : undefined}
          >
            <span className='flex items-center justify-center gap-2'>

            <Avatar sx={{ width: 32, height: 32 }}>
              {user?.first_name?.charAt(0).toUpperCase()}
            </Avatar>
        <p>{user?.role_name === "User" ? "Client" : "Admin"}</p>
            </span>
          </IconButton>
        </Tooltip>
      </Box>
      <Menu
        anchorEl={anchorEl}
        id="account-menu"
        open={open}
        onClose={handleClose}
        // onClick={handleClose}
        slotProps={{
          paper: {
            elevation: 0,
            sx: {
              overflow: 'visible',
              filter: 'drop-shadow(0px 2px 8px rgba(0,0,0,0.32))',
              mt: 1.5,
              '& .MuiAvatar-root': {
                width: 32,
                height: 32,
                ml: -0.5,
                mr: 1,
              },
              '&::before': {
                content: '""',
                display: 'block',
                position: 'absolute',
                top: 0,
                right: 14,
                width: 10,
                height: 10,
                bgcolor: 'background.paper',
                transform: 'translateY(-50%) rotate(45deg)',
                zIndex: 0,
              },
            },
          },
        }}
        transformOrigin={{ horizontal: 'right', vertical: 'top' }}
        anchorOrigin={{ horizontal: 'right', vertical: 'bottom' }}
      >
        <MenuItem
          style={{ display: 'flex', justifyContent: "center", alignItems: "center" }}
          onClick={handleClose}>
          My Account
        </MenuItem>
        <MenuItem
          style={{ display: 'flex', justifyContent: "center", alignItems: "center" }}
          onClick={handleClose}>
          Payment History
        </MenuItem>
        {/* <Divider /> */}
        <MenuItem
          style={{ display: 'flex', justifyContent: "center", alignItems: "center", backgroundColor: "#B92424", margin: '5px 10px', borderRadius: '20px', color: "white" }}
          onClick={handleClose}
        >
          <div
            className="flex items-center gap-2 cursor-pointer"
            onClick={(e) => e.stopPropagation()} // prevents the menu from closing
          >
            <LogoutWrapper>
              <button>
                <Logout fontSize="small" />
                Logout
              </button>
            </LogoutWrapper>
          </div>
        </MenuItem>
      </Menu>
    </React.Fragment>
  );
}
