import React from "react";
import { Menu, Bell, Power } from "lucide-react";
import LogoutWrapper from "@/features/auth/components/LogoutWrapper";
import { ThemeToggle } from "@/components/common/ThemeToggle";
import LanguageMenu from "@/components/common/LanguageMenu";
import { cn } from "@/utils/cn";
import useGetCurrentUser from "@/hooks/useGetCurrentUser";
import { toTitleCase } from "@/utils/textFormater";
import AccountMenu from "@/components/common/AccountMenu";

import SearchIcon from '@mui/icons-material/Search';
import InputBase from '@mui/material/InputBase';
import { styled, alpha } from '@mui/material/styles';

interface HeaderProps {
  isSidebarOpen: boolean;
  setIsSidebarOpen: (isOpen: boolean) => void;
  className: string;
}


const Search = styled('div')(({ theme }) => ({
  position: 'relative',
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  '&:hover': {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  },
  marginRight: theme.spacing(2),
  marginLeft: 0,
  width: '100%',

  [theme.breakpoints.up('sm')]: {
    marginLeft: theme.spacing(3),
    width: 'auto',
  },
}));

const SearchIconWrapper = styled('div')(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: 'inherit',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    border:'1px solid grey',
    borderRadius:'6rem',
    minWidth:`calc(60vw - 200px)`,
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('md')]: {
      width: '60ch',
    },
  },
}));

const Header: React.FC<HeaderProps> = ({
  isSidebarOpen,
  setIsSidebarOpen,
  className,
}) => {
  const { role } = useGetCurrentUser();
  return (
    <nav
      className={cn(
        `bg-background fixed top-0 right-0 border-b border-gray-200 h-[70px]  z-40`,
        className
      )}
    >
      <div className="sm:px-6 lg:px-8 flex items-center h-16">
        <button
          className="p-2"
          onClick={() => setIsSidebarOpen(!isSidebarOpen)}
        >
          {isSidebarOpen ? <Menu className="w-6 h-6" /> : <Menu className="w-6 h-6" />}
        </button>
        <Search>
            <SearchIconWrapper>
              <SearchIcon />
            </SearchIconWrapper>
            <StyledInputBase
              placeholder="Search"
              inputProps={{ 'aria-label': 'search' }}
            />
          </Search>
        <button
          className="lg:hidden p-2"
          onClick={() => setIsSidebarOpen(!isSidebarOpen)}
        >
          {isSidebarOpen ? "" : <Menu className="w-6 h-6" />}
        </button>

        {/* Spacer to push icons to the right */}
        <div className="flex-1">
          <div className="block">
            <h1 className="text-2xl font-bold">{role && toTitleCase(role)}</h1>
          </div>
        </div>

        {/* Notification and Logout Buttons (Right) */}
        <div className="flex items-center space-x-4">
          {/* <ThemeToggle /> */}
          {/* <button className="p-2 rounded-full hover:bg-muted/20">
            <Bell className="w-5 h-5 text-muted-foreground" />
          </button> */}
          {/* <LanguageMenu /> */}
          <AccountMenu />
          {/* <LogoutWrapper>
            <button className="p-2 rounded-full hover:bg-muted/20">
              <Power className="w-5 h-5 text-muted-foreground" />
            </button>
          </LogoutWrapper> */}
        </div>
      </div>
    </nav>
  );
};

export default Header;
