import { Routes, Route } from "react-router-dom";
import { clientRoutes } from "./RoutesConfig";
import { ProtectedRoute } from "./ProtectedRoute";
import ClientLayout from "@/features/client/components/ClientLayout";
import NotFoundPage from "@/components/common/NotFoundPage";
import WorkSpaceLayout from "@/features/client/components/WorkSpaceLayout";

export const ClientRoutes = () => {
  return (
    <Routes>
      {clientRoutes.map(({ path, element: Element, roles, permission, layout }) => {
        const LayoutComponent = layout === "workspace" ? WorkSpaceLayout : ClientLayout;
        return (
          <Route
            key={path}
            path={path}
            element={
              <ProtectedRoute roles={roles} permission={permission}>
                <LayoutComponent>
                  <Element />
                </LayoutComponent>
              </ProtectedRoute>
            }
          />
        );
      })}

      <Route
        path="*"
        element={
          <ClientLayout>
            <NotFoundPage />
          </ClientLayout>
        }
      />
    </Routes>
  );
};
