import React, { useMemo, useState } from "react";
import { useForm } from "react-hook-form";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { RootState } from "@/store";
import { useSelector } from "react-redux";
import { zodResolver } from "@hookform/resolvers/zod";
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormMessage,
} from "@/components/ui/form";
import {
  createWorkSpaceSchema,
  CreateWorkSpaceSchema,
} from "../schemas/createWorkSpace.schema";
import { toast } from "sonner";
const CreateWorkSpaceForm = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const selectUser = useMemo(() => (state: RootState) => state.auth, []);
  const user = useSelector(selectUser);

  const form = useForm<createWorkSpaceSchema>({
    resolver: zodResolver(CreateWorkSpaceSchema),
    mode: "onSubmit",
    defaultValues: {
      name: "",
      created_by: user?.user_id ?? undefined,
    },
  });

  // const handleSubmit = async (values: createWorkSpaceSchema) => {
  //   setIsLoading(true);
  //   setError(null);
  //   try {
  //     const response = await fetch("http://localhost:8001/api/workspace/", {
  //       method: "POST",
  //       headers: {
  //         Accept: "application/json",
  //         "Content-Type": "application/json",
  //       },
  //       body: JSON.stringify(values),
  //     });


  //     if (!response.ok) {
  //       const errorBody = await response.json();  // Parsing JSON response
  //       console.error("Server error response:", errorBody);
  //       // Check if the response contains a message and display it
  //       const errorMessage = errorBody.message || "Failed to create workspace"; // Fallback message
  //       toast.error(errorMessage);
  //       throw new Error(errorMessage);
  //     }

  //     const result = await response.json();
  //     console.log("Workspace created:", result);
  //     console.log(result, '177')
  //     toast.success(result?.message);
  //     setTimeout(() => {
  //       window.location.reload();
  //     }, 1000)

  //   } catch (err) {
  //     console.log(err)
  //     setError("Error creating workspace. Please try again.");
  //     // toast.error("Error creating workspace. Please try again.");

  //   } finally {
  //     setIsLoading(false);
  //   }
  // };

  const handleSubmit = async (values: createWorkSpaceSchema) => {
    setIsLoading(true);
    setError(null);

    const globalUrl = "http://localhost:8001/api/workspace/";
    const localUrl = "https://ac4ee0054010e6ee7a052c425e401a13.serveo.net/api/workspace/";

    try {
      const postToAPI = async (url: string) => {
        const response = await fetch(url, {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
          body: JSON.stringify(values),
        });

        if (!response.ok) {
          const errorBody = await response.json();
          console.error(`Error from ${url}:`, errorBody);
          const errorMessage = errorBody.message || `Failed to create workspace at ${url}`;
          throw new Error(errorMessage);
        }

        return response.json();
      };

      // Make both requests
      const [globalResult, localResult] = await Promise.all([
        postToAPI(globalUrl),
        postToAPI(localUrl),
      ]);

      // Only if both succeed
      toast.success(globalResult?.message || "Workspace created successfully");

      // setTimeout(() => {
      //   window.location.reload();
      // }, 1000);

    } catch (err: any) {
      console.error(err);
      setError(err.message || "Error creating workspace. Please try again.");
      toast.error(err.message || "Error creating workspace. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <Form {...form}>
      <div className="mb-8">
        <div className="text-2xl font-secondary font-bold text-[#062393]">
          Create New Workspace!
        </div>
      </div>

      <form onSubmit={form.handleSubmit(handleSubmit)} className="space-y-2">
        {/* Workspace Name */}
        <FormField
          control={form.control}
          name="name"
          render={({ field }) => (
            <FormItem>
              <FormControl>
                <Input
                  {...field}
                  hasError={!!form.formState.errors.name}
                  placeholder="Workspace Name*"
                  className="rounded-full"
                />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />

        {/* Error message */}
        {/* {error && <p className="text-red-500 text-sm">{error}</p>} */}

        <Button
          type="submit"
          className="w-full rounded-full"
          disabled={isLoading}
        >
          {isLoading ? "Creating..." : "Create Workspace"}
        </Button>
      </form>
    </Form>
  );
};

export default CreateWorkSpaceForm;
