import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "./alert-dialog";

interface ConfirmationAlertProps {
  children: React.ReactNode;
  title: string;
  description: string;
  onConfirm: () => void;
  isLoading?: boolean;
  successButton?: string;
  cancelButton?: string;
}

export function ConfirmationAlert({
  children,
  title,
  description,
  onConfirm,
  isLoading = false,
  successButton,
  cancelButton
}: ConfirmationAlertProps) {
  return (
    <AlertDialog >
      <AlertDialogTrigger asChild>{children}</AlertDialogTrigger>
      <AlertDialogContent className="bg-card max-w-sm sm:max-w-md">
        <AlertDialogHeader>
          <AlertDialogTitle>{title}</AlertDialogTitle>
          <AlertDialogDescription className="font-semibold">{description}</AlertDialogDescription>
        </AlertDialogHeader>
        <AlertDialogFooter>
            <AlertDialogAction
            onClick={onConfirm}
            disabled={isLoading}
            className="bg-[#43996A] text-white hover:text-white hover:bg-green-600 rounded-full flex items-center justify-center"
            >
            {isLoading ? "Loading..." : `${successButton ? successButton : "Continue"}`}
            </AlertDialogAction>
            <AlertDialogCancel 
            className="bg-[#B92424] text-white px-4 hover:text-white hover:bg-red-500 rounded-full flex items-center justify-center"
            >
            {cancelButton ? cancelButton : "Cancel"}
            </AlertDialogCancel>
        </AlertDialogFooter>
      </AlertDialogContent>
    </AlertDialog>
  );
}
