import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { persistReducer } from 'redux-persist';
import storage from 'redux-persist/lib/storage'; 
import { User } from '../types/auth.types';
import { PURGE } from 'redux-persist';

export interface AuthState {
  role_name: string|null;
  user_id: number|null;
  first_name: string|null;
last_name: string|null;
email: string|null;
user_created_date: string|null;
access_token: string|null;
refresh_token: string|null;
  isAuthenticated: boolean|null;
  isLoading: boolean|null;

  secret_key_verified:boolean|null;
}

const initialState: AuthState = {
  role_name: null,
  user_id: null,
  first_name: null,
last_name: null,
email: null,
user_created_date: null,
access_token: null,
refresh_token: null,
isAuthenticated: false,
  isLoading: false,

  secret_key_verified:false,
};

const authSlice = createSlice({
  name: 'auth',
  initialState,
  reducers: {
    setCredentials: (state, action: PayloadAction<{ 
      role_name: string;
      user_id: number;
      first_name: string;
      last_name: string;
      email: string;
      user_created_date: string;
      access_token: string;
      refresh_token: string;

      secret_key_verified:boolean;
    }>) => {
      const { 
        role_name,
        user_id,
        first_name,
        last_name,
        email,
        user_created_date,
        access_token,
        refresh_token,
        
        secret_key_verified, 
      } = action.payload;
    
      state.role_name = role_name;
      state.user_id = user_id;
      state.first_name = first_name;
      state.last_name = last_name;
      state.email = email;
      state.user_created_date = user_created_date;
      state.access_token = access_token;
      state.refresh_token = refresh_token;
      state.secret_key_verified=secret_key_verified;
      state.isAuthenticated = true;
    },
    logout: (state) => {
      state.role_name = null;
      state.user_id = null;
      state.first_name =null;
      state.last_name = null;
      state.email = null;
      state.user_created_date =null;
      state.access_token = null;
      state.refresh_token = null;
      state.secret_key_verified=false;
      state.isAuthenticated = false;
    },
    
    updateAccessToken: (state, action: PayloadAction<string>) => {
      state.access_token = action.payload;
    },
   
    setLoading: (state, action: PayloadAction<boolean>) => {
      state.isLoading = action.payload;
    },
  },
  // extraReducers: (builder) => {
  //   builder.addCase(PURGE, (state) => {
  //     state.user = null;
  //     state.accessToken = null;
  //     state.refreshToken = null;
  //     state.isAuthenticated = false;
  //   });
  // },
});

// Persist Config
const persistConfig = {
  key: 'auth',
  storage,
};

const persistedAuthReducer = persistReducer(persistConfig, authSlice.reducer);

export const { setCredentials, updateAccessToken, setLoading, logout } = authSlice.actions;

export default persistedAuthReducer;
