import { useLocation } from "react-router-dom";
import Sidebar from "@/components/layout/side-navigaion/SideNav";
import { ClientSideNavItems } from "./client-side-nav-items";

const SideNavigation = ({
  setIsSidebarOpen,
}: {
  setIsSidebarOpen: (isOpen: boolean) => void;
}) => {
  const location = useLocation();
  const pathname = location.pathname; // e.g., "/client/workspaces/123/sku/1"

  const pathSegments = pathname.split("/").filter(Boolean);
  const isSkuDetail = pathSegments.includes("sku");

  const workspaceId = pathSegments[pathSegments.indexOf("workspaces") + 1] || "unknown";
  const skuId = isSkuDetail ? pathSegments[pathSegments.indexOf("sku") + 1] : "unknown";

  return (
    <Sidebar
      navItems={ClientSideNavItems(isSkuDetail, workspaceId, skuId)}
      setIsSidebarOpen={setIsSidebarOpen}
    />
  );
};

export default SideNavigation;
