import { lazy } from "react";
import { ROUTES } from "../constants";
import CreateNewWorkSpace from "@/features/client/pages/workspace/CreateNewWorkSpace";

export const clientComponents = {
  Dashboard: lazy(
    () => import("@/features/client/pages/dashboard/page/DashboardPage")
  ),
  Assign: lazy(
    () =>
      import("@/features/client/pages/assign/assign-table/AssignCreationTable")
  ),
  ViewAll: lazy(
    () => import("@/features/client/pages/view-all/view-table/ViewAllTable")
  ),
  ViewAllWorkSpace: lazy(
    () => import("@/features/client/pages/workspace/ViewAllWorkSpace")
  ),
  CreateNewWorkSpace: lazy(
    () => import("@/features/client/pages/workspace/CreateNewWorkSpace")
  ),
  NewUser: lazy(
    () => import("@/features/co-admin/pages/n-user/n-user-table/NewUser")
  ),
  UpdateNewUser: lazy(
    () => import("@/features/co-admin/pages/n-user/new-user-creation-form/page")
  ),
  UpdateUser: lazy(
    () => import("@/features/co-admin/pages/n-user/user-creation-form/page")
  ),
  ViewAllSKU: lazy(
     () => import("@/features/client/pages/workspace/ViewAllSKU")
  ),
  ViewSkuDetails: lazy(
  () => import("@/features/client/pages/workspace/ViewSkuDetails")
),

};
export const clientRoutes = [
  {
    path: ROUTES.CLIENT.DASHBOARD,
    element: clientComponents.Dashboard,
    roles: ["client", "User"],
    permission: "view_dashboard",
    layout: "client", //  Specify layout
  },
  {
    path: ROUTES.CLIENT.VIEWALL,
    element: clientComponents.ViewAll,
    roles: ["client", "co-admin"],
    permission: "view_dashboard",
    layout: "client", //  Specify layout
  },
  {
    path: ROUTES.CLIENT.VIEWALLWORKSPACE,
    element: clientComponents.ViewAllWorkSpace,
    roles: ["maker", "co-admin"],
    permission: "view_dashboard",
    layout: "workspace", //  Use workspace layout
  },
  {
    path: ROUTES.CLIENT.CREATENEWWORKSPACE,
    element: clientComponents.CreateNewWorkSpace,
    roles: ["maker", "co-admin"],
    permission: "view_dashboard",
    layout: "workspace", //  Use workspace layout
  },
  {
  path: ROUTES.CLIENT.VIEWALLWORKSPACESKU, // /workspaces/sku
  element: clientComponents.ViewAllSKU,
  roles: ["maker", "co-admin"],
  permission: "view_dashboard",
  layout: "client",
},
{
  path: ROUTES.CLIENT.VIEWSKUDETAILS, // /workspaces/sku/:skuId
  element: clientComponents.ViewSkuDetails,
  roles: ["maker", "co-admin"],
  permission: "view_dashboard",
  layout: "client",
},

];
