import { useState } from "react";
import DashboardContentWrapper from "@/components/common/DashboardContentWrapper";
import ClientSideNavigation from "./side-navigation/ClientSideNav";
import WorkspaceHeader from "@/components/layout/side-navigaion/WorkspaceHeaderNav";
import { ReactNode } from "react";
import PageTitle from "@/components/layout/titles/PageTitle";

interface WorkSpaceLayoutProps {
  children: ReactNode;
}

const WorkSpaceLayout = ({ children }: WorkSpaceLayoutProps) => {
  const [isSidebarOpen, setIsSidebarOpen] = useState(false);

  return (
    <div className="flex min-h-screen bg-background">

      <WorkspaceHeader
        isSidebarOpen={isSidebarOpen}
        setIsSidebarOpen={setIsSidebarOpen}
        className="fixed top-0 w-full bg-background shadow-md"  
      />
       <main className="w-full h-[calc(100vh-70px)] mt-14 overflow-y-auto px-8" onClick={()=>{setIsSidebarOpen(false)}}>
     
        <DashboardContentWrapper>
          <PageTitle />
          {children}
        </DashboardContentWrapper>
      </main>
    </div>
  );
};

export default WorkSpaceLayout;
